var _this;

import chatInput from '../../components/im-chat/chatinput.vue'; //input框
import postData from '../../common/index/index.post.data.js';//朋友圈数据

import glbal from '../../common/global.js';

export default {
	components: {
		chatInput,
	},
	data() {
		return {
			// posts: postData,//模拟数据
			// user_id: 4,
			// username: 'Liuxy',
		
			page:{
				list:[]
			},

			index: '',
			comment_index: '',
			
			imageList:[],

			input_placeholder: '评论', //占位内容
			focus: false, //是否自动聚焦输入框
			is_reply: false, //回复还是评论
			showInput: false, //评论输入框

			screenHeight: '', //屏幕高度(系统)
			platform: '',
			windowHeight: '' ,//可用窗口高度(不计入软键盘)
			
			loadMoreText: "加载中...",
			showLoadMore: false,
			
			showDateSelect: false
		}
	},
	mounted() {
		_this= this;
		uni.getStorage({
			key: 'posts',
			success: function (res) {
				console.log(res.data);
				this.posts = res.data;
			}
		});

	},
	onLoad() {
		uni.getSystemInfo({ //获取设备信息
			success: (res) => {
				this.screenHeight = res.screenHeight;
				this.platform = res.platform;
			}
		});
		uni.startPullDownRefresh();
		
	},
	onShow() {
		uni.onWindowResize((res) => { //监听窗口尺寸变化,窗口尺寸不包括底部导航栏
			if(this.platform === 'ios'){
				this.windowHeight = res.size.windowHeight;
				this.adjust();
			}else{
				if (this.screenHeight - res.size.windowHeight > 60 && this.windowHeight <= res.size.windowHeight) {
					this.windowHeight = res.size.windowHeight;
					this.adjust();
				}
			}
		});
	},
	onHide() {
		uni.offWindowResize(); //取消监听窗口尺寸变化
	},
	onUnload() {
		this.max = 0,
			this.data = [],
			this.loadMoreText = "加载更多",
			this.showLoadMore = false;
	},
	onReachBottom() { //监听上拉触底事件
		console.log('onReachBottom');
		this.showLoadMore = true;
		setTimeout(() => {
			//获取数据
			if (this.page.currPage < this.page.totalPage){
				this.loadMoreText = "加载更多";
				this.page.currPage = this.page.currPage + 1;
				var url = _this.getSessionUrl("tb/tbpost/list");
				uni.request({
					url: url,
					method: 'GET',
					data: {
						page: this.page.currPage
					},
					success: res => {
						console.log(res);
						if (res.data.code != 0){
							uni.showToast({
								title:"数据获取失败！",
								"icon": "none"
							})
						}else{
							var page = res.data.page;
							_this.page.currPage = page.currPage;
							_this.page.pageSize = page.pageSize;
							_this.page.totalCount = page.totalCount;
							_this.page.totalPage = page.totalPage;
							
							_this.page.list = _this.page.list.concat(page.list);
						}
					},
					fail: () => {
						uni.hideLoading();
								uni.showToast({
									icon:'none',
									title:"发布失败,请检查网络"
						})
					},
					complete: () => {}
				});
				
			}else{
				this.loadMoreText = "暂无更多";
			}
		}, 1000);
	},
	onPullDownRefresh() { //监听下拉刷新动作
		console.log('onPullDownRefresh');
		// 这里获取数据
		setTimeout(function() {
			var url = _this.getSessionUrl("tb/tbpost/list");
			//初始化数据
			uni.request({
				url: url,
				method: 'GET',
				data: {
					
				},
				success: res => {
					console.log(res);
					if (res.data.code != 0){
						uni.showToast({
							title:"数据获取失败！",
							"icon": "none"
						})
					}else{
						_this.page = res.data.page;
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon:'none',
						title:"发布失败,请检查网络"
					})
				},
				complete: () => {}
			});
			
			
			var url1 = _this.getSessionUrl("tb/tbconfig/getCarousel");
			uni.request({
				url:url1,
				method:"GET",
				success: (res) => {
					console.log(res);
					if(res.data.code == 0){
						_this.imageList = res.data.imageList;
						console.log(_this.imageList);
					}
				}
				
			});
			
			uni.stopPullDownRefresh(); //停止下拉刷新
			
		}, 1000);
	},
	onNavigationBarButtonTap(e) {//监听标题栏点击事件
		var b = this.isLogin();
		if(!b){
			this.toLogin("?to=index");
		}else{
			if (e.index == 0) {
				this.navigateTo('../publish/publish')
			}	
		}
		
	},
	computed:{
		
	},
	methods: {
		test(){
			this.navigateTo('../test/test');
		},
		navigateTo(url) {
			uni.navigateTo({
				url: url
			});
		},
		like(index) {
			console.log(index + " like");
			
			var b = this.isLogin();
			
			if(!b){
				this.toLogin("?to=index");
			}else{
				var like = this.page.list[index].like;
				console.log("like:" + like);
				var url;
				if(like){
					url = this.getSessionUrl("tb/tblike/cancelLike");
				}else{
					url = this.getSessionUrl("tb/tblike/like");
				}
				uni.request({
					url: url,
					method: 'GET',
					data: {
						postId: _this.page.list[index].id,
					},
					success: res => {
						console.log(res);
						if (res.data.code == 0){
							_this.page.list[index].like = !like;
							_this.page.list[index].likeList = res.data.likeList;
						}else{
							if (res.data.msg == "用户未登陆"){
								_this.removeGlobalUser();
								_this.toLogin();
							}else{
								uni.showToast({
									icon:"none",
									title:"失败"
								})
							}
							
						}
					},
					fail: () => {
						uni.hideLoading();
						uni.showToast({
							icon:'none',
							title:"发布失败,请检查网络"
						})
					},
					complete: () => {}
				});
			}
			
			// if (this.posts[index].islike === 0) {
			// 	this.posts[index].islike = 1;
			// 	this.posts[index].like.push({
			// 		"uid": this.user_id,
			// 		"username": "," + this.username
			// 	});
			// } else {
			// 	this.posts[index].islike = 0;
			// 	this.posts[index].like.splice(this.posts[index].like.indexOf({
			// 		"uid": this.user_id,
			// 		"username": "," + this.username
			// 	}), 1);
			// }
			
		},
		comment(index) {
			
			var b = this.isLogin();
			if(!b){
				this.toLogin();
			}else{
				var url = this.getSessionUrl("tb/tbuser/isLogin");
				uni.request({
					url: url,
					method: 'GET',
					data: {},
					success: res => {
						if (res.data.code == 0){
							_this.showInput = true; //调起input框
							_this.focus = true;
							_this.index = index;
						}else{
							_this.removeGlobalUser();
							_this.toLogin();
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
			
		},
		adjust() { //当弹出软键盘发生评论动作时,调整页面位置pageScrollTo
			return;
			uni.createSelectorQuery().selectViewport().scrollOffset(res => {
				var scrollTop = res.scrollTop;
				let view = uni.createSelectorQuery().select("#post-" + this.index);
				view.boundingClientRect(data => {
					console.log("data:" + JSON.stringify(data));
					console.log("手机屏幕高度:" + this.screenHeight);
					console.log("竖直滚动位置" + scrollTop);
					console.log("节点离页面顶部的距离为" + data.top);
					console.log("节点高度为" + data.height);
					console.log("窗口高度为" + this.windowHeight);

					uni.pageScrollTo({
						scrollTop: scrollTop - (this.windowHeight - (data.height + data.top + 45)), //一顿乱算
						// scrollTop: 50, 
						duration: 300
					});
				}).exec();
			}).exec();
		},
		reply(index, comment_index) {
			
			var b = this.isLogin();
			if(!b){
				this.toLogin();
			}else{
				var url = this.getSessionUrl("tb/tbuser/isLogin");
				uni.request({
					url: url,
					method: 'GET',
					data: {},
					success: res => {
						if (res.data.code == 0){
							_this.is_reply = true; //回复中
							_this.showInput = true; //调起input框
							// let replyTo = _this.posts[index].comments.comment[comment_index].username;
							let replyTo = _this.page.list[index].commentList[comment_index].username;
							_this.input_placeholder = '回复' + replyTo;
							_this.index = index; //post索引
							_this.comment_index = comment_index; //评论索引
							_this.focus = true;
						}else{
							_this.removeGlobalUser();
							_this.toLogin();
						}
					},
					fail: () => {},
					complete: () => {}
				});
			}
			
			
		},
		blur: function() {
			this.init_input();
		},
		send_comment: function(message) {
			
			console.log(message);
			
			var comment_content = message.content;
			
			var url = this.getSessionUrl("tb/tbcomment/comment");
			uni.request({
				url: url,
				method: 'POST',
				data: {
					postId: this.page.list[this.index].id,
					content: comment_content,
					commentId: this.page.list[this.index].commentList[this.comment_index].id
				},
				success: res => {
					console.log(res);
					if (res.data.code == 0){
						_this.page.list[_this.index].commentList = res.data.commentList;
					}else{
						uni.showToast({
							icon:"none",
							title:"失败"
						})
					}
				},
				fail: () => {
					uni.hideLoading();
					uni.showToast({
						icon:'none',
						title:"发布失败,请检查网络"
					})
				},
				complete: () => {}
			});

			// if (this.is_reply) {
			// 	var reply_username = this.posts[this.index].comments.comment[this.comment_index].username;
			// 	var comment_content = '回复' + reply_username + ':' + message.content;
			// } else {
				
			// }
			// this.posts[this.index].comments.total += 1;
			// this.posts[this.index].comments.comment.push({
			// 	"uid": this.user_id,
			// 	"username": this.username,
			// 	"content": comment_content //直接获取input中的值
			// });
			this.init_input();
		},
		init_input() {
			this.showInput = false;
			this.focus = false;
			this.input_placeholder = '评论';
			this.is_reply = false;
		},
		previewImage(imageList, image_index) {
			var current = imageList[image_index];
			uni.previewImage({
				current: current,
				urls: imageList
			});
		},
		goPublish() {
			uni.navigateTo({
				url: './publish/publish',
				success: res => {},
				fail: () => {},
				complete: () => {}
			});
		},
		toTopic(topicId, topicTitle){
			uni.navigateTo({
				url: "/pages/topic/post?id=" + topicId + "&title=" + topicTitle
			})
		}
	}
}
